import { atom, useRecoilState, useRecoilValue } from 'recoil'

export const headerContentState = atom<React.ReactNode>({
  key: 'headerContentState',
  default: null,
})

export default function useHeaderContentState() {
  return useRecoilState(headerContentState)
}

export function useHeaderContent() {
  const value = useRecoilValue(headerContentState)
  return value
}
