import { atom, useRecoilState } from 'recoil'

type BreadcrumbItem = {
  href?: string
  name: string
}

export const breadcrumbState = atom<BreadcrumbItem[]>({
  key: 'breadcrumbState',
  default: [],
})

export default function useBreadcrumb() {
  const state = useRecoilState(breadcrumbState)
  return state
}
