import React, { Suspense } from 'react'
import { Switch, Route } from 'react-router-dom'
import { Layout } from 'antd'
import ErrorBoundary from '@/components/error-boundary'
import Exception from '@/components/exception'
import Loading from '@/components/loading'
import AppHeader from '@/components/app-header'
import { useRoutes, RouteItem } from '@/constants/routes'
import theme from '@/theme'

import { ReportTemplateList } from '@/containers/report-templates'

function App() {
  const routes = useRoutes()

  const renderRouteItem = React.useCallback((item: RouteItem) => {
    const path = `${item.path}${item.param ? `/${item.param}` : ''}`

    return (
      <Route
        exact
        key={item.id}
        path={path}
        component={
          item.component ? item.component : () => <h1> {item.title} </h1>
        }
      />
    )
  }, [])

  const renderRoute: any = React.useCallback(
    (data: RouteItem[]) => {
      return data.map((item: RouteItem) => {
        return [
          renderRouteItem(item),
          item.children && renderRoute(item.children),
          item.hide && renderRoute(item.hide),
        ]
      })
    },
    [renderRouteItem]
  )

  return (
    <ErrorBoundary>
      <Layout>
        <AppHeader />
        <Layout.Content
          style={{
            background: `linear-gradient(-90deg,#4252c4,${theme.primary})`,
          }}>
          <Suspense fallback={<Loading />}>
            <Switch>
              <Route exact path="/" component={ReportTemplateList} />
              {renderRoute(routes)}
              <Route component={Exception} />
            </Switch>
          </Suspense>
        </Layout.Content>
        <Layout.Footer
          className="pb-6 text-center text-lg text-gray-600"
          style={{ background: 'rgb(221, 228, 237)' }}>
          高雄榮總數位病理平台
        </Layout.Footer>
      </Layout>
    </ErrorBoundary>
  )
}

export default App
