import React from 'react'
import cx from 'classnames'
import { Layout, Avatar, Menu } from 'antd'
import {
  SettingOutlined,
  ExportOutlined,
  HomeOutlined,
} from '@ant-design/icons'
import styled from 'styled-components'
import Box from '@/components/box'
import Button from '@/components/button'
import Breadcrumb from '@/components/breadcrumb'
import useBreadcrumb from '@/hooks/useBreadcrumb'
import useHeaderContent from '@/hooks/useHeaderContent'
import history from '@/utils/history'
import { envStore } from '@/constants/env'
import * as routes from '@/constants/routes'

export default function AppHeader() {
  const [items] = useBreadcrumb()
  const [content] = useHeaderContent()

  const [current, setCurrentPath] = React.useState(history.location.pathname)

  const handleTo = (pathname?: string) => () => {
    if (pathname) {
      setCurrentPath(pathname)
      history.push(pathname)
    }
  }

  const getMenuClassName = (targetUrl: string) => {
    return cx({ 'is--current': targetUrl.includes(current) })
  }

  return (
    <Header>
      <Box className="flex items-center justify-between">
        <Box className="flex items-center">
          <Logo>
            <img
              src={`${envStore.publicUrl}/images/logo-sm@2x.png`}
              alt="app-logo"
              className="cursor-pointer"
            />
          </Logo>
          <Menu
            mode="horizontal"
            className="bg-transparent ml-3"
            style={{ borderBottom: 'none', lineHeight: '46px' }}>
            <MenuItem
              key={routes.RADIOL_IMAGES}
              onClick={handleTo(routes.RADIOL_IMAGES)}
              className={getMenuClassName(routes.RADIOL_IMAGES)}>
              <Box className="flex items-center text-lg">
                <SettingOutlined />
                清單查詢 +
              </Box>
            </MenuItem>
            <MenuItem
              key={routes.REPORT_INQUIRY}
              onClick={handleTo(routes.REPORT_INQUIRY)}
              className={getMenuClassName(routes.REPORT_INQUIRY)}>
              <Box className="flex items-center text-lg">
                <SettingOutlined />
                輸入報告 +
              </Box>
            </MenuItem>
            <MenuItem
              key={routes.REPORT_TEMPLATE_LIST}
              onClick={handleTo(routes.REPORT_TEMPLATE_LIST)}
              className={getMenuClassName(routes.REPORT_TEMPLATE_LIST)}>
              <Box className="flex items-center text-lg">
                <SettingOutlined />
                結構式報告 +
              </Box>
            </MenuItem>
            <MenuItem
              key="outside"
              onClick={handleTo(routes.REPORT_TEMPLATE_EDIT)}
              className={getMenuClassName('outside')}>
              <Box className="flex items-center text-lg">
                <SettingOutlined />
                醫學影像教學平台 +
              </Box>
            </MenuItem>
          </Menu>
        </Box>
        <Box className="flex items-center">
          <Box className="text-white text-lg mr-4">Hi, 使用者</Box>
          <Avatar className="flex items-center h-12 w-12">
            <Box className="text-2xl">C</Box>
          </Avatar>
          <Button
            icon={
              <ExportOutlined
                className="text-white text-2xl"
                style={{ marginRight: 0 }}
              />
            }
            className="ml-4 bg-transparent border border-white rounded pr-1 hover--none"
            style={{ minWidth: 35, borderColor: '#fff' }}
          />
        </Box>
      </Box>
      <Box className="mt-6 ml-4 pb-8 flex justify-between">
        <Breadcrumb separator=">" style={{ minHeight: 32 }}>
          <Breadcrumb.Item onClick={handleTo('/')}>
            <HomeOutlined className="relative" style={{ top: -4 }} />
          </Breadcrumb.Item>
          {items.map((x, idx) => (
            <Breadcrumb.Item
              key={`breadcrumb-${idx}`}
              onClick={handleTo(x.href)}>
              {x.name}
            </Breadcrumb.Item>
          ))}
        </Breadcrumb>
        {content}
      </Box>
    </Header>
  )
}

const Header = styled(Layout.Header)`
  &.ant-layout-header {
    height: auto;
    padding: 16px 1.7rem 16px 2.1rem;
    background: ${p => `linear-gradient(-90deg, #4252c4, ${p.theme.primary})`};
    color: ${p => p.theme.primary};
    box-shadow: 0 0 20px ${p => p.theme.purple150};
  }

  .anticon {
    cursor: pointer;
  }
`

const MenuItem = styled(Menu.Item)`
  &.ant-menu-item {
    margin: 0 5px !important;
    padding: 0 15px !important;
    border-radius: 6px;
    border-bottom: none !important;
    color: #fff;

    &.is--current,
    &:hover {
      color: #fff !important;
      background: rgba(36, 48, 144, 1);
    }
  }
`

const Logo = styled.div``
